import styled from "styled-components";
import { FavoritesIcon } from "assets";
import { Media } from "ui/intex";
import { Color } from "ui/colors";
import { motion } from "framer-motion";

const StyledBlogItem = styled(motion.li)`
  list-style-type: none;

  height: 388px;
  background-color: ${Color.White};
  border-radius: 16px;
  box-shadow: 0px 0px 10px 3px rgba(29, 42, 52, 0.2);

  &:hover {
    box-shadow: 0px 0px 20px 10px rgba(24, 42, 56, 0.3);
    transition: all 1s;
  }

  ${Media.LG} {
    width: 328px;
  }

  ${Media.MD} {
    width: 272px;
  }
`;

const Image = styled.img`
  width: 100%;
  height: 208px;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
`;

const Content = styled.div`
  margin: 20px 20px;
`;

const CustomDate = styled.h3`
  margin-bottom: 5px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: ${Color.Medium};

  ${Media.MD} {
    font-size: 14px;
  }
`;

const Title = styled.h2`
  height: 84px;
  font-weight: 600;
  font-size: 18px;
  line-height: 28px;
  color: ${Color.Secondary};

  ${Media.MD} {
    font-size: 14px;
  }
`;

const Button = styled.button`
  position: relative;
  left: 90%;
  background: none;
  cursor: pointer;
`;

const FavoriteIcon = styled(FavoritesIcon)`
  path {
    stroke: ${Color.Secondary};
  }

  :hover {
    fill: red;
  }
  :active,
  :focus {
    fill: white;
  }
`;

export { StyledBlogItem, Image, Content, CustomDate, Title, Button, FavoriteIcon };
